import * as React from 'react';

import { Avatar, Box, Chip, ListItemDecorator, Typography } from '@mui/joy';
import Select from '@mui/joy/Select';
import Option from '@mui/joy/Option';
import IDataSource from '../models/IDataSource';

type ChallengeSourceSelectProps = {
    dataSources: IDataSource[];
};

const ChallengeSourceSelect: React.FunctionComponent<ChallengeSourceSelectProps> = (props: ChallengeSourceSelectProps) => {

    return (
        <Select
            defaultValue="Button Press"
            componentsProps={{
                listbox: {
                    sx: {
                        '--List-decorator-width': '48px',
                    },
                },
            }}
            sx={{
                '--List-decorator-width': '48px',
                minWidth: 240,
            }}
        >
            {props.dataSources.map((data, index) => (
                <Option
                    key={data.id}
                    value={data.name}
                    label={data.name} // The appearance of the selected value will be a string
                >
                    <ListItemDecorator>
                        <Avatar src={`/static/images/avatar/${index + 1}.jpg`} />
                    </ListItemDecorator>
                    <Box component="span" sx={{ display: 'block' }}>
                        <Typography component="span">{data.name}</Typography>
                        <Typography level="body4">{data.description}</Typography>
                    </Box>
                    <Chip
                        size="sm"
                        variant="outlined"
                        color={data.color}
                        sx={{
                            ml: 'auto',
                            borderRadius: '2px',
                            minHeight: '20px',
                            paddingInline: '4px',
                            fontSize: 'xs',
                            bgcolor: `${data.color}.softBg`,
                        }}
                    >
                        {data.integration}
                    </Chip>
                </Option>
            ))}
        </Select>
    );
};

export default ChallengeSourceSelect;
