import * as React from 'react';
import { GlobalStyles } from '@mui/system';
import { CssVarsProvider } from '@mui/joy/styles';

import { Box, Chip, List, ListItemDecorator, Theme } from '@mui/joy';

import Tabs from '@mui/joy/Tabs';
import TabList from '@mui/joy/TabList';
import TabPanel from '@mui/joy/TabPanel';
import Tab, { tabClasses } from '@mui/joy/Tab';

// Icons import
import SportsScoreRoundedIcon from '@mui/icons-material/SportsScoreRounded';
import HomeOutlined from '@mui/icons-material/HomeOutlined';
import FavoriteBorder from '@mui/icons-material/FavoriteBorder';
import Search from '@mui/icons-material/Search';
import SearchRounded from '@mui/icons-material/SearchRounded';

// custom
import teamTheme from '../theme';
import Layout from '../components/Layout';

import NavigationBar from '../components/NavigationBar';
import TopHeaderBar from '../components/TopHeaderBar';
import ChallengeFilter from '../components/ChallengeFilter';
import Loading from '../components/Loading';

import IDataSource from '../models/IDataSource';
import IChallengeDefinition from '../models/IChallengeDefinition';

import ServiceFactory from '../services/ServiceFactory';
import ChallengeDetail from '../components/ChallengeDetail';

const ChallengesPage: React.FunctionComponent = () => {
    const [drawerOpen, setDrawerOpen] = React.useState(false);
    const [dataSources, setDataSources] = React.useState<IDataSource[] | undefined>(undefined);
    const [activeChallenges, setActiveChallenges] = React.useState<IChallengeDefinition[] | undefined>(undefined);
    const [likedChallenges, setLikedChallenges] = React.useState<IChallengeDefinition[] | undefined>(undefined);
    const [index, setIndex] = React.useState(0);

    React.useEffect(() => {
        const loadDataSourcesAsync = async () => {
            const service = ServiceFactory.BuildDataSourceService();
            const results = await service.fetchAllAsync();
            setDataSources(results);
        };

        const loadActiveChallengesAsync = async () => {
            const service = ServiceFactory.BuildChallengeDefinitionService();
            const results = await service.fetchActiveAsync();
            setActiveChallenges(results);
        };

        const loadLikedChallengesAsync = async () => {
            const service = ServiceFactory.BuildChallengeDefinitionService();
            const results = await service.fetchLikedAsync();
            setLikedChallenges(results);
        };

        if (!dataSources) loadDataSourcesAsync();
        if (!activeChallenges) loadActiveChallengesAsync();
        if (!likedChallenges) loadLikedChallengesAsync();
    }, []);

    return (
        <CssVarsProvider disableTransitionOnChange theme={teamTheme}>
            <GlobalStyles<Theme>
                styles={(theme) => ({
                    body: {
                        margin: 0,
                        fontFamily: theme.vars.fontFamily.body,
                    },
                })}
            />
            {drawerOpen && (
                <Layout.SideDrawer onClose={() => setDrawerOpen(false)}>
                    <NavigationBar selected="challenges" />
                </Layout.SideDrawer>
            )}
            <Layout.Root
                useSimpleLayout={true}
                sx={{
                    ...(drawerOpen && {
                        height: '100vh',
                        overflow: 'hidden',
                    }),
                }}
            >
                <TopHeaderBar title="Challenges" icon={<SportsScoreRoundedIcon />} onDrawerOpen={() => setDrawerOpen(true)} />
                <Layout.SideNav>
                    <NavigationBar selected="challenges" />
                </Layout.SideNav>
                {/* <Layout.SidePane>{dataSources ? <ChallengeFilter dataSources={dataSources} /> : <Loading />}</Layout.SidePane> */}
                <Layout.Main>
                    <Box
                        sx={{
                            //bgcolor: 'background.componentBg',
                            flexGrow: 1,
                            // m: -3,
                            // p: 3,
                            overflowX: 'hidden',
                            borderRadius: 'md',
                        }}
                    >
                        <Tabs aria-label="Pipeline" value={index} onChange={(event, value) => setIndex(value as number)}>
                            <TabList>
                                <Tab>
                                    <ListItemDecorator sx={{ mt: '2px' }}>
                                        <HomeOutlined />
                                    </ListItemDecorator>
                                    Active{' '}
                                    <Chip size="sm" variant="soft" color={index === 0 ? 'primary' : 'neutral'} sx={{ ml: 1 }}>
                                        2
                                    </Chip>
                                </Tab>
                                <Tab>
                                    <ListItemDecorator sx={{ mt: '2px' }}>
                                        <FavoriteBorder />
                                    </ListItemDecorator>
                                    Likes{' '}
                                    <Chip size="sm" variant="soft" color={index === 1 ? 'primary' : 'neutral'} sx={{ ml: 1 }}>
                                        2
                                    </Chip>
                                </Tab>
                                <Tab>
                                    <ListItemDecorator sx={{ mt: '2px' }}>
                                        <Search />
                                    </ListItemDecorator>
                                    Search
                                </Tab>
                            </TabList>
                            <Box
                                sx={(theme) => ({
                                    '--bg': theme.vars.palette.background.level1,
                                    background: 'var(--bg)',
                                    // boxShadow: '0 0 0 100vmax var(--bg)',
                                    // clipPath: 'inset(0 -100vmax)',
                                    // px: 1.5,
                                    // py: 2,
                                })}
                            >
                                <TabPanel value={0}>
                                    <List
                                        sx={{
                                            display: 'grid',
                                            gridTemplateColumns: 'repeat(auto-fill, minmax(300px, 1fr))',
                                            gap: 2,
                                        }}
                                    >
                                        {activeChallenges ? activeChallenges.map((c: IChallengeDefinition) => <ChallengeDetail challenge={c} key={c.id} />) : null}
                                    </List>
                                </TabPanel>
                                <TabPanel value={1}>
                                    <List
                                        sx={{
                                            display: 'grid',
                                            gridTemplateColumns: 'repeat(auto-fill, minmax(300px, 1fr))',
                                            gap: 2,
                                        }}
                                    >
                                        {likedChallenges ? likedChallenges.map((c: IChallengeDefinition) => <ChallengeDetail challenge={c} key={c.id} />) : null}
                                    </List>
                                </TabPanel>
                                <TabPanel value={2}>{dataSources ? <ChallengeFilter dataSources={dataSources} /> : <Loading />}</TabPanel>
                            </Box>
                        </Tabs>
                    </Box>
                </Layout.Main>
            </Layout.Root>
        </CssVarsProvider>
    );
};

export default ChallengesPage;
