import * as React from 'react';
import { GlobalStyles } from '@mui/system';
import { CssVarsProvider } from '@mui/joy/styles';
import type { Theme } from '@mui/joy/styles';

import { List } from '@mui/joy';

// Icons import
import EmojiEventsRoundedIcon from '@mui/icons-material/EmojiEventsRounded';

// custom
import teamTheme from '../theme';
import Layout from '../components/Layout';

import NavigationBar from '../components/NavigationBar';
import TopHeaderBar from '../components/TopHeaderBar';
import IAchievementInstance from '../models/IAchievementInstance';
import ServiceFactory from '../services/ServiceFactory';
import AchievementDetail from '../components/AchievementDetail';

const AchievementsPage: React.FunctionComponent = () => {
    const [drawerOpen, setDrawerOpen] = React.useState(false);
    const [achievements, setAchievements] = React.useState<IAchievementInstance[] | undefined>(undefined);

    React.useEffect(() => {
        const loadAchievementsAsync = async () => {
            const service = ServiceFactory.BuildAchievementService();
            const results = await service.fetchMyAsync();
            setAchievements(results);
        };

        if (!achievements) loadAchievementsAsync();
    }, []);

    return (
        <CssVarsProvider disableTransitionOnChange theme={teamTheme}>
            <GlobalStyles<Theme>
                styles={(theme: Theme) => ({
                    body: {
                        margin: 0,
                        fontFamily: theme.vars.fontFamily.body,
                    },
                })}
            />
            {drawerOpen && (
                <Layout.SideDrawer onClose={() => setDrawerOpen(false)}>
                    <NavigationBar selected="achievements" />
                </Layout.SideDrawer>
            )}
            <Layout.Root
                useSimpleLayout={true}
                sx={{
                    ...(drawerOpen && {
                        height: '100vh',
                        overflow: 'hidden',
                    }),
                }}
            >
                <TopHeaderBar title="Achievements" icon={<EmojiEventsRoundedIcon />} onDrawerOpen={() => setDrawerOpen(true)} />
                <Layout.SideNav>
                    <NavigationBar selected="achievements" />
                </Layout.SideNav>
                <Layout.Main>
                    <List
                        sx={{
                            display: 'grid',
                            gridTemplateColumns: 'repeat(auto-fill, minmax(300px, 1fr))',
                            gap: 2,
                        }}
                    >
                        {achievements ? achievements.map((a: IAchievementInstance) => <AchievementDetail achievement={a} key={a.id} />) : null}
                    </List>
                </Layout.Main>
            </Layout.Root>
        </CssVarsProvider>
    );
};

export default AchievementsPage;
