import { useNavigate } from 'react-router-dom';
import { Chip, ListItem, ListItemButton, ListItemContent, ListItemDecorator } from '@mui/joy';

type NavigationBarItemProps = {
    isSelected?: boolean;
    label?: string;
    text: string;
    link: string;
    icon: JSX.Element;
};

const NavigationBarItem: React.FunctionComponent<NavigationBarItemProps> = (props: NavigationBarItemProps) => {
    const navigate = useNavigate();
    const navigateTo = (link: string) => navigate(`/${link}`);

    const renderBetaChip = () =>
        props.label ? (
            <Chip variant="soft" size="sm" sx={{ borderRadius: 'sm' }}>
                {props.label}
            </Chip>
        ) : null;

    return (
        <ListItem>
            {props.isSelected ? (
                <ListItemButton variant="soft" color="primary" onClick={() => navigateTo(props.link)}>
                    <ListItemDecorator sx={{ color: 'inherit' }}>{props.icon}</ListItemDecorator>
                    <ListItemContent>{props.text}</ListItemContent>
                    {renderBetaChip()}
                </ListItemButton>
            ) : (
                <ListItemButton onClick={() => navigateTo(props.link)}>
                    <ListItemDecorator sx={{ color: 'neutral.500' }}>{props.icon}</ListItemDecorator>
                    <ListItemContent>{props.text}</ListItemContent>
                    {renderBetaChip()}
                </ListItemButton>
            )}
        </ListItem>
    );
};

export default NavigationBarItem;
