import * as React from 'react';
import { Box, TextField, Typography } from '@mui/joy';
import IconButton from '@mui/joy/IconButton';

// Icons import
import SearchRoundedIcon from '@mui/icons-material/SearchRounded';
import GridViewRoundedIcon from '@mui/icons-material/GridViewRounded';
import MailRoundedIcon from '@mui/icons-material/MailRounded';
import MenuIcon from '@mui/icons-material/Menu';
import BookRoundedIcon from '@mui/icons-material/BookRounded';

// Custom
import Menu from './Menu';
import Layout from './Layout';
import ColorSchemeToggle from './ColorSchemeToggle';

type TopHeaderBarProps = {
    title: string;
    icon: JSX.Element;
    onDrawerOpen: () => void;
};

const TopHeaderBar: React.FunctionComponent<TopHeaderBarProps> = (props: TopHeaderBarProps) => {
    return (
        <Layout.Header>
            <Box
                sx={{
                    display: 'flex',
                    flexDirection: 'row',
                    alignItems: 'center',
                    gap: 1.5,
                }}
            >
                <IconButton variant="outlined" size="sm" onClick={() => props.onDrawerOpen()} sx={{ display: { sm: 'none' } }}>
                    <MenuIcon />
                </IconButton>
                <IconButton size="sm" variant="solid" sx={{ display: { xs: 'none', sm: 'inline-flex' } }}>
                    {props.icon}
                </IconButton>
                <Typography component="h1" fontWeight="xl">
                    {props.title}
                </Typography>
            </Box>
            <TextField
                size="sm"
                placeholder="Search anything"
                startDecorator={<SearchRoundedIcon color="primary" />}
                endDecorator={
                    <IconButton variant="outlined" size="sm" color="neutral">
                        <Typography fontWeight="lg" fontSize="sm" textColor="text.tertiary">
                            /
                        </Typography>
                    </IconButton>
                }
                sx={{
                    flexBasis: '500px',
                    display: {
                        xs: 'none',
                        sm: 'flex',
                    },
                }}
            />
            <Box sx={{ display: 'flex', flexDirection: 'row', gap: 1.5 }}>
                <IconButton size="sm" variant="outlined" color="primary" sx={{ display: { xs: 'inline-flex', sm: 'none' } }}>
                    <SearchRoundedIcon />
                </IconButton>
                <IconButton size="sm" variant="outlined" color="primary" component="a" href="/blog/first-look-at-joy/">
                    <BookRoundedIcon />
                </IconButton>
                <Menu
                    id="app-selector"
                    control={
                        <IconButton size="sm" variant="outlined" color="primary" aria-label="Apps">
                            <GridViewRoundedIcon />
                        </IconButton>
                    }
                    menus={[
                        {
                            label: 'Messages',
                            active: true,
                            href: '/joy-ui/getting-started/templates/email/',
                            'aria-current': 'page',
                        },
                        {
                            label: 'Team',
                            href: '/joy-ui/getting-started/templates/team/',
                        },
                        {
                            label: 'Files',
                            href: '/joy-ui/getting-started/templates/files/',
                        },
                    ]}
                />
                <ColorSchemeToggle />
            </Box>
        </Layout.Header>
    );
};

export default TopHeaderBar;
