import {
    Avatar,
    Box,
    Button,
    Chip,
    List,
    ListDivider,
    ListItem,
    ListItemContent,
    ListItemDecorator,
    Sheet,
    Typography,
} from '@mui/joy';


// Icons import
import KeyboardArrowRightRoundedIcon from '@mui/icons-material/KeyboardArrowRightRounded';
import IUser from '../models/IUser';

type UserDetailProps = {
    user: IUser;
    key: string;
};

const UserDetail: React.FunctionComponent<UserDetailProps> = (props: UserDetailProps) => {

    const { user, key } = props;

    return (
        <Sheet
            key={key}
            component="li"
            variant="outlined"
            sx={{
                bgcolor: 'background.componentBg',
                borderRadius: 'sm',
                p: 2,
                listStyle: 'none',
            }}
        >
            <Box sx={{ display: 'flex', gap: 2 }}>
                <Avatar src={user.avatar} srcSet={user.avatar2x} sx={{ borderRadius: 'sm' }} />
                <Box>
                    <Typography>{user.name}</Typography>
                    <Typography level="body3">{user.role}</Typography>
                </Box>
            </Box>
            <ListDivider component="div" sx={{ my: 2 }} />
            <List sx={{ '--List-decorator-width': '48px' }}>
                <ListItem sx={{ alignItems: 'flex-start' }}>
                    <ListItemDecorator
                        sx={{
                            '&:before': {
                                content: '""',
                                position: 'absolute',
                                height: '100%',
                                width: '2px',
                                bgcolor: 'divider',
                                left: 'calc(var(--List-item-paddingLeft) + 15px)',
                                top: '50%',
                            },
                        }}
                    >
                        <Avatar size="sm" src="https://seeklogo.com/images/D/dribbble-logo-143FF96D65-seeklogo.com.png" />
                    </ListItemDecorator>
                    <ListItemContent>
                        <Typography level="body2">Senior designer</Typography>
                        <Typography level="body3">Dribbble</Typography>
                    </ListItemContent>
                    <Typography level="body2">2015-now</Typography>
                </ListItem>
                <ListItem sx={{ alignItems: 'flex-start' }}>
                    <ListItemDecorator>
                        <Avatar size="sm" src="https://seeklogo.com/images/P/pinterest-logo-CA98998DCB-seeklogo.com.png" />
                    </ListItemDecorator>
                    <ListItemContent>
                        <Typography level="body2">Designer</Typography>
                        <Typography level="body3">Pinterest</Typography>
                    </ListItemContent>
                    <Typography level="body2">2012-2015</Typography>
                </ListItem>
            </List>
            <Button size="sm" variant="plain" endIcon={<KeyboardArrowRightRoundedIcon fontSize="small" />} sx={{ px: 1, mt: 1 }}>
                Expand
            </Button>
            <ListDivider component="div" sx={{ my: 2 }} />
            <Typography fontSize="sm">Skills tags:</Typography>
            <Box sx={{ mt: 1.5, display: 'flex', gap: 1 }}>
                <Chip variant="outlined" color="neutral" size="sm" sx={{ borderRadius: 'sm' }}>
                    UI design
                </Chip>
                <Chip variant="outlined" color="neutral" size="sm" sx={{ borderRadius: 'sm' }}>
                    Illustration
                </Chip>
            </Box>
        </Sheet>
    );
};

export default UserDetail;
