import { Avatar, Box, Button, Chip, List, ListDivider, ListItem, ListItemContent, ListItemDecorator, Sheet, Typography } from '@mui/joy';

// Icons import
import KeyboardArrowRightRoundedIcon from '@mui/icons-material/KeyboardArrowRightRounded';
import IAchievementInstance from '../models/IAchievementInstance';
import { getRuleTypeIconUrl } from '../models/RuleType';
import { getAchievementEntryTypeName } from '../models/AchievementEntryType';
import IAchievementEntry from '../models/IAchievementEntry';

type AchievementDetailProps = {
    achievement: IAchievementInstance;
    key: string;
};

const AchievementDetail: React.FunctionComponent<AchievementDetailProps> = (props: AchievementDetailProps) => {
    const { achievement, key } = props;

    const renderEntry = (entry: IAchievementEntry, index: Number) => {
        const sx =
            index === 0
                ? {
                      '&:before': {
                          content: '""',
                          position: 'absolute',
                          height: '100%',
                          width: '2px',
                          bgcolor: 'divider',
                          left: 'calc(var(--List-item-paddingLeft) + 15px)',
                          top: '50%',
                      },
                  }
                : undefined;

        return (
            <ListItem key={index.toString()} sx={{ alignItems: 'flex-start' }}>
                <ListItemDecorator sx={sx}>
                    <Avatar size="sm" src={getRuleTypeIconUrl(entry.entryType)} />
                </ListItemDecorator>
                <ListItemContent>
                    <Typography level="body2">{getAchievementEntryTypeName(entry.entryType)}</Typography>
                    {/* <Typography level="body3">{getRuleTypeDescription(achievement, rule)}</Typography> */}
                </ListItemContent>
                {/* <Typography level="body2">2015-now</Typography> */}
            </ListItem>
        );
    };

    const renderLabel = (label: string): JSX.Element => {
        return (
            <Chip variant="outlined" color="neutral" size="sm" sx={{ borderRadius: 'sm' }}>
                {label}
            </Chip>
        );
    };

    return (
        <Sheet
            key={key}
            component="li"
            variant="outlined"
            sx={{
                bgcolor: 'background.componentBg',
                borderRadius: 'sm',
                p: 2,
                listStyle: 'none',
            }}
        >
            <Box sx={{ display: 'flex', gap: 2 }}>
                <Avatar src={achievement.avatar} srcSet={achievement.avatar2x} sx={{ borderRadius: 'sm' }} />
                <Box>
                    <Typography>{achievement.name}</Typography>
                    <Typography level="body3">{achievement.description}</Typography>
                </Box>
            </Box>
            <ListDivider component="div" sx={{ my: 2 }} />
            <List sx={{ '--List-decorator-width': '48px' }}>{achievement.entries.map((r, index) => renderEntry(r, index))}</List>
            <Box
                sx={{
                    display: 'flex',
                    justifyContent: 'space-between',
                    alignItems: 'center',
                    flexWrap: 'wrap',
                    gap: 2,
                }}
            >
                <Button size="sm" variant="plain" endIcon={<KeyboardArrowRightRoundedIcon fontSize="small" />} sx={{ px: 1, mt: 1 }}>
                    Expand
                </Button>
            </Box>
            <ListDivider component="div" sx={{ my: 2 }} />
            <Typography fontSize="sm">Labels</Typography>
            <Box sx={{ mt: 1.5, display: 'flex', gap: 1 }}>{achievement.labels.map((l) => renderLabel(l))}</Box>
        </Sheet>
    );
};

export default AchievementDetail;
