import { Avatar, Box, Button, Chip, List, ListDivider, ListItem, ListItemContent, ListItemDecorator, Sheet, Typography } from '@mui/joy';
import IconButton from '@mui/joy/IconButton';

// Icons import
import KeyboardArrowRightRoundedIcon from '@mui/icons-material/KeyboardArrowRightRounded';
import IChallengeDefinition from '../models/IChallengeDefinition';
import IRuleEntry from '../models/IRuleEntry';
import { getRuleTypeDescription, getRuleTypeIconUrl, getRuleTypeName } from '../models/RuleType';
import { FavoriteBorder } from '@mui/icons-material';

type ChallengeDetailProps = {
    challenge: IChallengeDefinition;
    key: string;
};

const ChallengeDetail: React.FunctionComponent<ChallengeDetailProps> = (props: ChallengeDetailProps) => {
    const { challenge, key } = props;

    const renderRule = (rule: IRuleEntry, index: Number) => {
        const sx =
            index === 0
                ? {
                      '&:before': {
                          content: '""',
                          position: 'absolute',
                          height: '100%',
                          width: '2px',
                          bgcolor: 'divider',
                          left: 'calc(var(--List-item-paddingLeft) + 15px)',
                          top: '50%',
                      },
                  }
                : undefined;

        return (
            <ListItem key={index.toString()} sx={{ alignItems: 'flex-start' }}>
                <ListItemDecorator sx={sx}>
                    <Avatar size="sm" src={getRuleTypeIconUrl(rule.entry)} />
                </ListItemDecorator>
                <ListItemContent>
                    <Typography level="body2">{getRuleTypeName(rule)}</Typography>
                    <Typography level="body3">{getRuleTypeDescription(challenge, rule)}</Typography>
                </ListItemContent>
                {/* <Typography level="body2">2015-now</Typography> */}
            </ListItem>
        );
    };

    const renderLabel = (label: string): JSX.Element => {
        return (
            <Chip variant="outlined" color="neutral" size="sm" sx={{ borderRadius: 'sm' }}>
                {label}
            </Chip>
        );
    };

    return (
        <Sheet
            key={key}
            component="li"
            variant="outlined"
            sx={{
                bgcolor: 'background.componentBg',
                borderRadius: 'sm',
                p: 2,
                listStyle: 'none',
            }}
        >
            <Box sx={{ display: 'flex', gap: 2 }}>
                <Avatar src={challenge.avatar} srcSet={challenge.avatar2x} sx={{ borderRadius: 'sm' }} />
                <Box>
                    <Typography>{challenge.name}</Typography>
                    <Typography level="body3">{challenge.description}</Typography>
                </Box>
            </Box>
            <ListDivider component="div" sx={{ my: 2 }} />
            <List sx={{ '--List-decorator-width': '48px' }}>{challenge.rules.map((r, index) => renderRule(r, index))}</List>
            <Box
                sx={{
                    display: 'flex',
                    justifyContent: 'space-between',
                    alignItems: 'center',
                    flexWrap: 'wrap',
                    gap: 2,
                }}
            >
                <Button size="sm" variant="plain" endIcon={<KeyboardArrowRightRoundedIcon fontSize="small" />} sx={{ px: 1, mt: 1 }}>
                    Expand
                </Button>
                <IconButton size="sm" variant="plain" sx={{ mr: 2 }}>
                    <FavoriteBorder fontSize="small" />
                </IconButton>
            </Box>
            <ListDivider component="div" sx={{ my: 2 }} />
            <Typography fontSize="sm">Labels</Typography>
            <Box sx={{ mt: 1.5, display: 'flex', gap: 1 }}>{challenge.labels.map((l) => renderLabel(l))}</Box>
        </Sheet>
    );
};

export default ChallengeDetail;
