import * as React from 'react';
import Box from '@mui/joy/Box';
import Typography from '@mui/joy/Typography';
import Avatar from '@mui/joy/Avatar';
import List from '@mui/joy/List';
import ListDivider from '@mui/joy/ListDivider';
import ListItem from '@mui/joy/ListItem';
import ListItemButton from '@mui/joy/ListItemButton';
import ListItemDecorator from '@mui/joy/ListItemDecorator';
import ITeam from '../models/ITeam';
import Loading from './Loading';

type TeamListProps = {
    teams: ITeam[] | undefined;
    selectedTeamId: string | undefined;
    onTeamSelected: (team: ITeam) => void;
};

const TeamList: React.FunctionComponent<TeamListProps> = (props: TeamListProps) => {
    const { teams, selectedTeamId, onTeamSelected } = props;

    if (!teams) return <Loading />;

    const renderListItem = (item: ITeam, isSelected: boolean) => {
        return (
            <ListItem>
                <ListItemButton {...(isSelected && { variant: 'soft', color: 'primary' })} onClick={() => onTeamSelected(item)} sx={{ p: 2 }}>
                    <ListItemDecorator sx={{ alignSelf: 'flex-start' }}>
                        <Avatar alt="" src={item.avatar} srcSet={item.avatar2x} sx={{ borderRadius: 'sm' }} />
                    </ListItemDecorator>
                    <Box sx={{ pl: 2, width: '100%' }}>
                        <Box
                            sx={{
                                display: 'flex',
                                justifyContent: 'space-between',
                                mb: 0.5,
                            }}
                        >
                            <Typography level="body3">{item.name}</Typography>
                            <Typography level="body3" textColor="text.tertiary">
                                1/1/2022
                            </Typography>
                        </Box>
                        <Box>
                            <Typography sx={{ mb: 0.5 }}>{item.name}</Typography>
                            <Typography level="body2">{item.description}</Typography>
                        </Box>
                    </Box>
                </ListItemButton>
            </ListItem>
        );
    };

    return (
        <List>
            {teams
                ? teams.map((item: ITeam, index: number) => (
                      <React.Fragment key={index}>
                          {renderListItem(item, item.id === selectedTeamId)}
                          <ListDivider sx={{ m: 0 }} />
                      </React.Fragment>
                  ))
                : null}
        </List>
    );
};

export default TeamList;
