import IChallengeDefinition from "./IChallengeDefinition";
import IRuleEntry from "./IRuleEntry";

export const RuleTypeList = [
    'Undefined',
    'ActiveDays',
    'StartTime',
    'Duration',
    'RaceToScore',
    'RaceToZero',
    'MaxScoreDaily',
    'MaxScoreWeekly',
    'MaxScoreMonthly',
    'MinScoreDaily',
    'MinScoreWeekly',
    'MinScoreMonthly',
    'FirstToScoreDaily',
    'FirstToScoreWeekly',
    'FirstToScoreMonthly',
    'ApprovalProcess',
    'Prerequisite',
];
export type RuleType = typeof RuleTypeList[number];

export function getRuleTypeName(rule: IRuleEntry): string {
    switch (rule.entry) {
        case 'ActiveDays':
            return 'Active Days';
        case 'StartTime':
            return 'Start Time';
        case 'Duration':
            return 'Duration';
        case 'RaceToScore':
            return 'Race to Score';
        case 'RaceToZero':
            return 'Race to Zero';
        case 'MaxScoreDaily':
            return 'Maximum Score Daily';
        case 'MaxScoreWeekly':
            return 'Maximum Score Weekly';
        case 'MaxScoreMonthly':
            return 'Maximum Score Monthly';
        case 'MinScoreDaily':
            return 'Minimum Score Daily';
        case 'MinScoreWeekly':
            return 'Minimum Score Weekly';
        case 'MinScoreMonthly':
            return 'Minimum Score Monthly';
        case 'FirstToScoreDaily':
            return 'First to Score Daily';
        case 'FirstToScoreWeekly':
            return 'First to Score Weekly';
        case 'FirstToScoreMonthly':
            return 'First to Score Monthly';
        case 'ApprovalProcess':
            return 'Approval Process';
        case 'Prerequisite':
            return 'Prerequisite';
        case 'Undefined':
        default:
            return 'Undefined Rule';
    }
}

export function getRuleTypeDescription(challenge: IChallengeDefinition, rule: IRuleEntry): string {
    switch (rule.entry) {
        case 'ActiveDays':
            return 'Challenge is only running on certain days of the week';
        case 'StartTime':
            return `Challenge starts at ${rule.value}`;
        case 'Duration':
            return `Challenge runs for a set duration: ${rule.value}`;
        case 'RaceToScore':
            return `Race to score: ${rule.value} ${challenge.unit}`;
        case 'RaceToZero':
            return `Score starts at value ${rule.value} and decreases to zero`;
        case 'MaxScoreDaily':
            return `Players are limited to ${rule.value} ${challenge.unit} each day`;
        case 'MaxScoreWeekly':
            return `Players are limited to ${rule.value} ${challenge.unit} each week`;
        case 'MaxScoreMonthly':
            return `Players are limited to ${rule.value} ${challenge.unit} each month`;
        case 'MinScoreDaily':
            return 'Players will automatically receive a minimum number of points if they scored lower';
        case 'MinScoreWeekly':
            return 'Players will automatically receive a minimum number of points if they scored lower';
        case 'MinScoreMonthly':
            return 'Players will automatically receive a minimum number of points if they scored lower';
        case 'FirstToScoreDaily':
            return 'Players will receive bonus points if they are first to a certain amount of points in a day';
        case 'FirstToScoreWeekly':
            return 'Players will receive bonus points if they are first to a certain amount of points in a week';
        case 'FirstToScoreMonthly':
            return 'Players will receive bonus points if they are first to a certain amount of points in a month';
        case 'ApprovalProcess':
            return 'Players points must be approved by a team admin';
        case 'Prerequisite':
            return 'Has another challenge as a pre-requisite';
        case 'Undefined':
        default:
            return 'Undefined Rule';
    }
}

