import * as React from 'react';
import { GlobalStyles } from '@mui/system';
import { CssVarsProvider, Theme } from '@mui/joy/styles';

import { Box, List, Typography } from '@mui/joy';
import IconButton from '@mui/joy/IconButton';

// Icons import
import GroupRoundedIcon from '@mui/icons-material/GroupRounded';
import KeyboardArrowDownRoundedIcon from '@mui/icons-material/KeyboardArrowDownRounded';

// custom
import teamTheme from '../theme';
import Layout from '../components/Layout';
import NavigationBar from '../components/NavigationBar';
import TopHeaderBar from '../components/TopHeaderBar';
import UserDetail from '../components/UserDetail';

import ITeam from '../models/ITeam';
import ServiceFactory from '../services/ServiceFactory';
import TeamList from '../components/TeamList';
import IUser from '../models/IUser';

const TeamsPage: React.FunctionComponent = () => {
    const [drawerOpen, setDrawerOpen] = React.useState(false);
    const [teams, setTeams] = React.useState<ITeam[] | undefined>(undefined);
    const [users, setUsers] = React.useState<IUser[] | undefined>(undefined);
    const [teamUsers, setTeamUsers] = React.useState<IUser[] | undefined>(undefined);
    const [selectedTeamId, setSelectedTeamId] = React.useState<string | undefined>(undefined);

    function onlyUnique(value: string, index: number, self: string[]) {
        return self.indexOf(value) === index;
    }

    React.useEffect(() => {
        const loadTeamsAsync = async () => {
            const teamService = ServiceFactory.BuildTeamService();
            const results = await teamService.fetchMyTeamsAsync();
            setTeams(results);

            const userIds = results.flatMap((r) => r.playerIds.concat(r.managerIds)).filter(onlyUnique);

            const userService = ServiceFactory.BuildUserService();
            const users = await userService.fetchByIdsAsync(userIds);

            setUsers(users);

            if (results.length > 0) {
                onTeamSelected(results[0]);
            }
        };

        if (!teams) loadTeamsAsync();
    }, []);

    const onTeamSelected = (team: ITeam) => {
        setSelectedTeamId(team.id);
        if (users) {
            const userIds = team.playerIds.concat(team.managerIds).filter(onlyUnique);
            // console.log(team.name + ' selected');
            const thisTeamUsers = users.filter((u) => userIds.indexOf(u.id) >= 0);
            // console.log(thisTeamUsers);
            setTeamUsers(thisTeamUsers);
        }
    };

    return (
        <CssVarsProvider disableTransitionOnChange theme={teamTheme}>
            <GlobalStyles<Theme>
                styles={(theme) => ({
                    body: {
                        margin: 0,
                        fontFamily: theme.vars.fontFamily.body,
                    },
                })}
            />
            {drawerOpen && (
                <Layout.SideDrawer onClose={() => setDrawerOpen(false)}>
                    <NavigationBar selected="teams" />
                </Layout.SideDrawer>
            )}
            <Layout.Root
                useSimpleLayout={false}
                sx={{
                    ...(drawerOpen && {
                        height: '100vh',
                        overflow: 'hidden',
                    }),
                }}
            >
                <TopHeaderBar title="Teams" icon={<GroupRoundedIcon />} onDrawerOpen={() => setDrawerOpen(true)} />
                <Layout.SideNav>
                    <NavigationBar selected="teams" />
                </Layout.SideNav>
                <Layout.SidePane>
                    <Box
                        sx={{
                            p: 2,
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'space-between',
                        }}
                    >
                        <Typography
                            textColor="neutral.500"
                            fontWeight={700}
                            sx={{
                                fontSize: '10px',
                                textTransform: 'uppercase',
                                letterSpacing: '.1rem',
                            }}
                        >
                            Your Teams
                        </Typography>
                        <IconButton size="sm" variant="plain" color="primary" sx={{ '--IconButton-size': '24px' }}>
                            <KeyboardArrowDownRoundedIcon fontSize="small" color="primary" />
                        </IconButton>
                    </Box>
                    <TeamList teams={teams} onTeamSelected={onTeamSelected} selectedTeamId={selectedTeamId} />
                </Layout.SidePane>
                <Layout.Main>
                    <List
                        sx={{
                            display: 'grid',
                            gridTemplateColumns: 'repeat(auto-fill, minmax(300px, 1fr))',
                            gap: 2,
                        }}
                    >
                        {teamUsers ? teamUsers.map((u: IUser) => <UserDetail user={u} key={u.id} />) : null}
                    </List>
                </Layout.Main>
            </Layout.Root>
        </CssVarsProvider>
    );
};

export default TeamsPage;
