import AchievementServiceMock from "./AchievementServiceMock";
import ChallengeDefinitionServiceMock from "./ChallengeDefinitionServiceMock";
import DataSourceServiceMock from "./DataSourceServiceMock";
import DeviceServiceMock from "./DeviceServiceMock";
import IAchievementService from "./interfaces/IAchievementService";
import IChallengeDefinitionService from "./interfaces/IChallengeDefinitionService";
import IDataSourceService from "./interfaces/IDataSourceService";
import IDeviceService from "./interfaces/IDeviceService";
import ITeamService from "./interfaces/ITeamService";
import IUserService from "./interfaces/IUserService";
import TeamServiceMock from "./TeamServiceMock";
import UserServiceMock from './UserServiceMock';

export default class ServiceFactory {
    public static BuildTeamService = (): ITeamService => new TeamServiceMock();
    public static BuildUserService = (): IUserService => new UserServiceMock();
    public static BuildDataSourceService = (): IDataSourceService => new DataSourceServiceMock();
    public static BuildChallengeDefinitionService = (): IChallengeDefinitionService => new ChallengeDefinitionServiceMock();
    public static BuildAchievementService = (): IAchievementService => new AchievementServiceMock();
    public static BuildDeviceService = (): IDeviceService => new DeviceServiceMock();
}
