import * as React from 'react';
import { GlobalStyles } from '@mui/system';
import { CssVarsProvider } from '@mui/joy/styles';
import type { Theme } from '@mui/joy/styles';

import { Avatar, Box, List, ListDivider, ListItem, Sheet, Typography } from '@mui/joy';

// Icons import
import AccountCircleRoundedIcon from '@mui/icons-material/AccountCircleRounded';

// custom
import teamTheme from '../theme';
import Layout from '../components/Layout';

import NavigationBar from '../components/NavigationBar';
import TopHeaderBar from '../components/TopHeaderBar';

const AccountPage: React.FunctionComponent = () => {
    const [drawerOpen, setDrawerOpen] = React.useState(false);
    return (
        <CssVarsProvider disableTransitionOnChange theme={teamTheme}>
            <GlobalStyles<Theme>
                styles={(theme) => ({
                    body: {
                        margin: 0,
                        fontFamily: theme.vars.fontFamily.body,
                    },
                })}
            />
            {drawerOpen && (
                <Layout.SideDrawer onClose={() => setDrawerOpen(false)}>
                    <NavigationBar selected="account" />
                </Layout.SideDrawer>
            )}
            <Layout.Root
                useSimpleLayout={true}
                sx={{
                    ...(drawerOpen && {
                        height: '100vh',
                        overflow: 'hidden',
                    }),
                }}
            >
                <TopHeaderBar title="My Account" icon={<AccountCircleRoundedIcon />} onDrawerOpen={() => setDrawerOpen(true)} />
                <Layout.SideNav>
                    <NavigationBar selected="account" />
                </Layout.SideNav>
                <Layout.Main>
                    <Sheet
                        variant="outlined"
                        sx={{
                            minHeight: 500,
                            borderRadius: 'sm',
                            p: 2,
                            mb: 3,
                            bgcolor: 'background.componentBg',
                        }}
                    >
                        <Box sx={{ display: 'block', textAlign: 'center', gap: 2, p: 3 }}>
                            <Avatar
                                size="lg"
                                src="https://i.pravatar.cc/40?img=54"
                                srcSet="https://i.pravatar.cc/80?img=54 2x"
                                sx={{
                                    margin: 'auto',
                                    borderRadius: '50%',
                                    minWidth: '200px',
                                    minHeight: '200px',
                                }}
                            />
                            <Box sx={{ display: 'block', p: 3 }}>
                                <Typography level="h3">Andrew Smith</Typography>
                                <Typography level="body3">Designer</Typography>
                            </Box>
                        </Box>
                        <ListDivider component="div" sx={{ my: 2 }} />
                        <Box sx={{ p: 3 }}>
                            <List sx={{}}>
                                <ListItem>
                                    <Box sx={{ pl: 2, width: '100%' }}>
                                        <Box
                                            sx={{
                                                display: 'flex',
                                                justifyContent: 'space-between',
                                                mb: 0.5,
                                                maxWidth: '400px',
                                            }}
                                        >
                                            <Typography level="body2" textColor="text.tertiary">
                                                Email
                                            </Typography>
                                            <Typography level="body3">andrew.smith@gmail.com</Typography>
                                        </Box>
                                    </Box>
                                </ListItem>
                            </List>
                        </Box>
                    </Sheet>
                </Layout.Main>
            </Layout.Root>
        </CssVarsProvider>
    );
};

export default AccountPage;
