import IChallengeDefinition from '../models/IChallengeDefinition';
import ActiveData from './data/challenges_active.json';
import LikedData from './data/challenges_liked.json';
import IChallengeDefinitionService from './interfaces/IChallengeDefinitionService';

export default class ChallengeDefinitionServiceMock implements IChallengeDefinitionService {
    async fetchActiveAsync(): Promise<IChallengeDefinition[]> {
        return ActiveData;
    }

    async fetchLikedAsync(): Promise<IChallengeDefinition[]> {
        return LikedData;
    }
}
