import { Box, List, ListItem, Typography } from '@mui/joy';
import IconButton from '@mui/joy/IconButton';

import AccountCircleRoundedIcon from '@mui/icons-material/AccountCircleRounded';
import DeveloperBoardRoundedIcon from '@mui/icons-material/DeveloperBoardRounded';
import EmojiEventsRoundedIcon from '@mui/icons-material/EmojiEventsRounded';
import KeyboardArrowDownRoundedIcon from '@mui/icons-material/KeyboardArrowDownRounded';
import MailRoundedIcon from '@mui/icons-material/MailRounded';
import PeopleRoundedIcon from '@mui/icons-material/PeopleRounded';
import SportsScoreRoundedIcon from '@mui/icons-material/SportsScoreRounded';

import NavigationBarItem from './NavigationBarItem';

type NavigationBarProps = {
    selected: string;
};

const NavigationBar: React.FunctionComponent<NavigationBarProps> = (props: NavigationBarProps) => {
    const { selected } = props;

    return (
        <List size="sm" sx={{ '--List-item-radius': '8px' }}>
            <ListItem nested sx={{ p: 0 }}>
                <Box
                    sx={{
                        mb: 1,
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'space-between',
                    }}
                >
                    <Typography
                        id="nav-list-browse"
                        textColor="neutral.500"
                        fontWeight={700}
                        sx={{
                            fontSize: '10px',
                            textTransform: 'uppercase',
                            letterSpacing: '.1rem',
                        }}
                    >
                        Browse
                    </Typography>
                    <IconButton size="sm" variant="plain" color="primary" sx={{ '--IconButton-size': '24px' }}>
                        <KeyboardArrowDownRoundedIcon fontSize="small" color="primary" />
                    </IconButton>
                </Box>
                <List
                    aria-labelledby="nav-list-browse"
                    sx={{
                        '& .JoyListItemButton-root': { p: '8px' },
                    }}
                >
                    <NavigationBarItem text="Challenges" link="challenges" isSelected={selected === 'challenges'} icon={<SportsScoreRoundedIcon fontSize="small" />} />
                    <NavigationBarItem
                        text="Achievements"
                        link="achievements"
                        isSelected={selected === 'achievements'}
                        label="Beta"
                        icon={<EmojiEventsRoundedIcon fontSize="small" />}
                    />
                    <NavigationBarItem text="Teams" link="teams" isSelected={selected === 'teams'} icon={<PeopleRoundedIcon fontSize="small" />} />
                    <NavigationBarItem text="Devices" link="files" isSelected={selected === 'files'} label="Demo" icon={<DeveloperBoardRoundedIcon fontSize="small" />} />
                    <NavigationBarItem text="Messages" link="messages" isSelected={selected === 'messages'} icon={<MailRoundedIcon fontSize="small" />} />
                    <NavigationBarItem text="My Account" link="account" isSelected={selected === 'account'} icon={<AccountCircleRoundedIcon fontSize="small" />} />
                </List>
            </ListItem>
        </List>
    );
};

export default NavigationBar;
