import { Box, Typography } from "@mui/joy";
import Card from "@mui/joy/Card";
import CardContent from "@mui/joy/CardContent";
import CardCover from "@mui/joy/CardCover";
import IconButton from "@mui/joy/IconButton";

import type { Theme } from '@mui/joy/styles';

import EditOutlinedIcon from '@mui/icons-material/EditOutlined';
import IDevice from "../models/IDevice";

type DeviceCardProps = {
    device: IDevice;
    onSelected: (device: IDevice) => void;
    key: string;
};

const DeviceCard: React.FunctionComponent<DeviceCardProps> = (props: DeviceCardProps) => {
    const { device, key, onSelected } = props;

    return (
        <Card
            key={key}
            onClick={() => onSelected(device)}
            sx={{
                '--Card-radius': (theme: Theme) => theme.vars.radius.sm,
                boxShadow: 'none',
                minHeight: '200px',
                '&:hover': {
                    cursor: 'pointer',
                },
            }}
        >
            <CardCover>
                <img alt="" src={device.avatar} />
            </CardCover>
            <CardCover
                sx={{
                    background: 'linear-gradient(to top, rgba(0,0,0,0.8), rgba(0,0,0,0.12))',
                }}
            />
            <CardContent
                sx={{
                    mt: 'auto',
                    flexGrow: 0,
                    flexDirection: 'row',
                    alignItems: 'center',
                }}
            >
                <Box sx={{ flex: 1 }}>
                    <Typography textColor="#fff">{device.name}</Typography>
                    <Typography level="body3" mt={0.5} textColor="rgba(255,255,255,0.72)">
                        Added {device.addedDate}
                    </Typography>
                </Box>
                <IconButton variant="plain" color="neutral" sx={{ color: '#fff' }}>
                    <EditOutlinedIcon />
                </IconButton>
            </CardContent>
        </Card>
    );
};

export default DeviceCard;
