import * as React from 'react';
import { GlobalStyles } from '@mui/system';
import { CssVarsProvider } from '@mui/joy/styles';
import type { Theme } from '@mui/joy/styles';
import Box from '@mui/joy/Box';
import Typography from '@mui/joy/Typography';
import IconButton from '@mui/joy/IconButton';

// Icons import
import MailRoundedIcon from '@mui/icons-material/MailRounded';
import KeyboardArrowDownRoundedIcon from '@mui/icons-material/KeyboardArrowDownRounded';

// custom
import emailTheme from '../theme';
import Layout from '../components/Layout';
import NavigationBar from '../components/NavigationBar';
import Mails from '../components/EmailList';
import EmailContent from '../components/EmailContent';
import TopHeaderBar from '../components/TopHeaderBar';

export default function EmailExamplePage() {
    const [drawerOpen, setDrawerOpen] = React.useState(false);
    return (
        <CssVarsProvider disableTransitionOnChange theme={emailTheme}>
            <GlobalStyles<Theme>
                styles={(theme) => ({
                    body: {
                        margin: 0,
                        fontFamily: theme.vars.fontFamily.body,
                    },
                })}
            />
            {drawerOpen && (
                <Layout.SideDrawer onClose={() => setDrawerOpen(false)}>
                    <NavigationBar selected="messages" />
                </Layout.SideDrawer>
            )}
            <Layout.Root
                useSimpleLayout={false}
                sx={{
                    ...(drawerOpen && {
                        height: '100vh',
                        overflow: 'hidden',
                    }),
                }}
            >
                <TopHeaderBar title="Messages" icon={<MailRoundedIcon />} onDrawerOpen={() => setDrawerOpen(true)} />
                <Layout.SideNav>
                    <NavigationBar selected="messages" />
                </Layout.SideNav>
                <Layout.SidePane>
                    <Box
                        sx={{
                            p: 2,
                            mb: 1,
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'space-between',
                        }}
                    >
                        <Typography
                            textColor="neutral.500"
                            fontWeight={700}
                            sx={{
                                fontSize: '10px',
                                textTransform: 'uppercase',
                                letterSpacing: '.1rem',
                            }}
                        >
                            Unread
                        </Typography>
                        <IconButton size="sm" variant="plain" color="primary" sx={{ '--IconButton-size': '24px' }}>
                            <KeyboardArrowDownRoundedIcon fontSize="small" color="primary" />
                        </IconButton>
                    </Box>
                    <Box sx={{ py: 10 }}>
                        <Typography textColor="text.tertiary" level="body2" sx={{ textAlign: 'center' }}>
                            You&apos;ve read all messages in your inbox.
                        </Typography>
                    </Box>
                    <Box
                        sx={{
                            p: 2,
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'space-between',
                        }}
                    >
                        <Typography
                            textColor="neutral.500"
                            fontWeight={700}
                            sx={{
                                fontSize: '10px',
                                textTransform: 'uppercase',
                                letterSpacing: '.1rem',
                            }}
                        >
                            Everything else
                        </Typography>
                        <IconButton size="sm" variant="plain" color="primary" sx={{ '--IconButton-size': '24px' }}>
                            <KeyboardArrowDownRoundedIcon fontSize="small" color="primary" />
                        </IconButton>
                    </Box>
                    <Mails />
                </Layout.SidePane>
                <Layout.Main>
                    <EmailContent />
                </Layout.Main>
            </Layout.Root>
        </CssVarsProvider>
    );
}
