import * as React from 'react';

import { Box, Button, ListDivider, Switch, TextField, Typography } from '@mui/joy';

import IconButton from '@mui/joy/IconButton';

// Icons import
import KeyboardArrowUpRoundedIcon from '@mui/icons-material/KeyboardArrowUpRounded';
import ChallengeSourceSelect from './ChallengeSourceSelect';

import IDataSource from '../models/IDataSource';

type ChallengeFilterProps = {
    dataSources: IDataSource[];
};

const ChallengeFilter: React.FunctionComponent<ChallengeFilterProps> = (props: ChallengeFilterProps) => {
    return (
        <div>
            <Box
                sx={{
                    p: 2,
                    pb: 1,
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'space-between',
                }}
            >
                <Typography fontSize="xs2" textColor="text.tertiary" textTransform="uppercase" letterSpacing="md" fontWeight="lg">
                    Filter by
                </Typography>
                <Button size="sm" variant="plain" sx={{ fontSize: 'xs', px: 1 }}>
                    Clear filters
                </Button>
            </Box>
            <Box sx={{ p: 2 }}>
                <Box
                    sx={{
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'space-between',
                    }}
                >
                    <Typography level="body2" textColor="text.primary">
                        By keywords
                    </Typography>
                    <IconButton size="sm" variant="plain" color="primary" sx={{ '--IconButton-size': '24px' }}>
                        <KeyboardArrowUpRoundedIcon fontSize="small" color="primary" />
                    </IconButton>
                </Box>
                <Box sx={{ mt: 2 }}>
                    <TextField placeholder="Source, title, etc" />
                </Box>
            </Box>
            <Box sx={{ p: 2 }}>
                <Box
                    sx={{
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'space-between',
                    }}
                >
                    <Typography level="body2" textColor="text.primary">
                        Challenge Source
                    </Typography>
                    <IconButton size="sm" variant="plain" color="primary" sx={{ '--IconButton-size': '24px' }}>
                        <KeyboardArrowUpRoundedIcon fontSize="small" color="primary" />
                    </IconButton>
                </Box>
                <Box sx={{ mt: 2 }}>
                    <ChallengeSourceSelect dataSources={props.dataSources} />
                </Box>
            </Box>
            <ListDivider component="hr" />
        </div>
    );
};

export default ChallengeFilter;
