import * as React from 'react';
import { Sheet } from '@mui/joy';
import CircleIcon from '@mui/icons-material/Circle';
import ICalendarStatus from '../models/ICalendarStatus';

type DeviceStatusProps = {
    id: string;
};

const DeviceStatus: React.FunctionComponent<DeviceStatusProps> = (props: DeviceStatusProps) => {
    const calendarState: ICalendarStatus = {
        id: props.id,
        columns: 6,
        state: [
            0xffffff, 0xffffff, 0xffffff, 0xffffff, 0xffffff, 0xffffff,
            0xffffff, 0xffffff, 0xffffff, 0xffffff, 0xffffff, 0xffffff,
            0xffffff, 0xffffff, 0xffffff, 0xffffff, 0xffffff, 0xffffff,
            0xffffff, 0xffffff, 0xffffff, 0xffffff, 0xffffff, 0xffffff,
            0xffffff, 0xffffff, 0xffffff, 0xffffff, 0xffffff, 0xffffff,
            0xffffff, 0xffffff, 0xffffff, 0xffffff, 0xffffff, 0xffffff,
            0xffffff, 0xffffff, 0xffffff, 0xffffff, 0xffffff, 0xffffff,
            0xffffff, 0xffffff, 0xffffff, 0xffffff, 0xffffff, 0xffffff,
            0xffffff, 0xffffff, 0xffffff, 0xffffff, 0xffffff, 0xffffff,
            0xffffff, 0xffffff, 0xffffff, 0xffffff, 0xffffff, 0xffffff,
            0xffffff, 0xffffff, 0xffffff, 0xffffff, 0xffffff, 0xffffff,
            0xffffff, 0xffffff, 0xffffff, 0xffffff, 0xffffff, 0xffffff,
            0xffffff, 0xffffff, 0xffffff, 0xffffff, 0xffffff, 0xffffff,
            0xffffff, 0xffccff, 0xffffff, 0xffffff, 0xffffff, 0xffffff,
            0xffffff, 0xffccff, 0xccffff, 0xffffcc, 0xffffff, 0xffffff,
            0xffccff, 0xffccff, 0xccffff, 0xffffcc, 0xffffff, 0xffffff,
        ],
    };

    return (
        <Sheet
            variant="outlined"
            sx={{
                //width: "60%",
                borderRadius: 'sm',
                gridColumn: '1/-1',
                bgcolor: 'background.componentBg',
                display: { xs: 'none', sm: 'grid' },
                gridTemplateColumns: '1fr 1fr 1fr 1fr 1fr 1fr',
                '& > *': {
                    px: 2,
                    py: 0.8,
                },
            }}
        >
            {calendarState.state.map((s) => (
                <CircleIcon sx={{ color: `#${s}` }} />
            ))}
        </Sheet>
    );
};

export default DeviceStatus;
