export const AchievementEntryTypeList = [
    'Undefined',
    'StartTime',
    'TotalAchieved',
];
export type AchievementEntryType = typeof AchievementEntryTypeList[number];

export function getAchievementEntryTypeName(entryType: AchievementEntryType): string {
    switch (entryType) {
        case 'TotalAchieved':
            return 'Total Achieved';
        case 'StartTime':
            return 'Start Time';
        case 'Undefined':
        default:
            return 'Undefined Rule';
    }
}

export default AchievementEntryType;
