import * as React from 'react';
import { GlobalStyles } from '@mui/system';
import { CssVarsProvider } from '@mui/joy/styles';
import type { Theme } from '@mui/joy/styles';
import Box from '@mui/joy/Box';

// Icons import
import DeveloperBoardRoundedIcon from '@mui/icons-material/DeveloperBoardRounded';

// custom
import filesTheme from '../theme';
import Layout from '../components/Layout';
import NavigationBar from '../components/NavigationBar';
import TopHeaderBar from '../components/TopHeaderBar';
import DeviceCard from '../components/DeviceCard';
import DeviceDetail from '../components/DeviceDetail';
import IDevice from '../models/IDevice';
import ServiceFactory from '../services/ServiceFactory';

const DevicesPage: React.FunctionComponent = () => {
    const [drawerOpen, setDrawerOpen] = React.useState(false);
    const [devices, setDevices] = React.useState<IDevice[] | undefined>(undefined);
    const [selectedDevice, setSelectedDevice] = React.useState<IDevice | undefined>(undefined);

    React.useEffect(() => {
        const loadDevicesAsync = async () => {
            const service = ServiceFactory.BuildDeviceService();
            const results = await service.fetchAllAsync();
            setDevices(results);
            if (results.length > 0) {
                setSelectedDevice(results[0]);
            }
        };

        if (!devices) loadDevicesAsync();
    }, []);

    const onDeviceSelected = (device: IDevice) => {
        setSelectedDevice(device);
    };

    return (
        <CssVarsProvider disableTransitionOnChange theme={filesTheme}>
            <GlobalStyles<Theme>
                styles={(theme) => ({
                    body: {
                        margin: 0,
                        fontFamily: theme.vars.fontFamily.body,
                    },
                })}
            />
            {drawerOpen && (
                <Layout.SideDrawer onClose={() => setDrawerOpen(false)}>
                    <NavigationBar selected="files" />
                </Layout.SideDrawer>
            )}
            <Layout.Root
                useSimpleLayout={false}
                sx={{
                    gridTemplateColumns: {
                        xs: '1fr',
                        sm: 'minmax(64px, 200px) minmax(450px, 1fr)',
                        md: 'minmax(160px, 300px) minmax(600px, 1fr) minmax(300px, 420px)',
                    },
                    ...(drawerOpen && {
                        height: '100vh',
                        overflow: 'hidden',
                    }),
                }}
            >
                <TopHeaderBar title="Devices" icon={<DeveloperBoardRoundedIcon />} onDrawerOpen={() => setDrawerOpen(true)} />
                <Layout.SideNav>
                    <NavigationBar selected="files" />
                </Layout.SideNav>
                <Layout.Main>
                    <Box
                        sx={{
                            display: 'grid',
                            gridTemplateColumns: 'repeat(auto-fit, minmax(240px, 1fr))',
                            gap: 2,
                        }}
                    >
                        {devices ? devices.map((d: IDevice) => <DeviceCard device={d} onSelected={onDeviceSelected} key={d.id} />) : null}
                    </Box>
                </Layout.Main>
                {selectedDevice ? <DeviceDetail device={selectedDevice} /> : null}
            </Layout.Root>
        </CssVarsProvider>
    );
};

export default DevicesPage;
